section > button {
  display: flex;
  color: white;
  background-color: var(--bgBaseSecondary);
  border: 1px solid var(--bgBaseSecondary);
  padding: 12px;
  margin: 1rem auto;
  text-transform: uppercase;
  border-radius: 20px;
  transition: linear background-color 0.2s, linear border 0.2s;
}

section > button:hover {
  background-color: var(--bgBaseTerciary);
  border: 1px solid var(--bgBaseTerciary);
}

.containerGridSectionWrapper {
  display: grid;
  overflow: hidden;
  height: auto;
  justify-content: center;
}

.containerGridSectionWrapper > div {
  margin: 0rem 1rem 1rem 0rem;
}

.containerGridSectionWrapper img {
  width: 100%;
  height: 200px;
  transition: linear box-shadow 0.2s;
  box-shadow: 5px 5px 10px var(--bgBaseSecondary);
}

.containerGridSectionWrapper img:hover {
  box-shadow: 5px 5px 10px var(--bgBasePrimary);
}

.columnCourses {
  grid-template-columns: repeat(4, 25%);
}

.columnProjects {
  grid-template-columns: repeat(2, 50%);
}

@media only screen and (max-width: 600px) {
  .columnProjects,
  .columnCourses {
    grid-template-columns: repeat(1, 100%);
  }

  .containerGridSectionWrapper img {
    height: 190px;
  }
}
